@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans&display=swap);
:root {
  --violet: #5964e0;
  --light-violet: #939bf4;
  --very-dark-blue: #19202d;
  --midnight: #121721;
  --white: #fff;
  --light-gray: #f4f6f8;
  --gray: #9daec2;
  --dark-gray: #6e8098;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Kumbh Sans", sans-serif;
}

a {
  text-decoration: none;
}

body .App {
  height: 100vh;
}

body .App.light {
  background-color: #f4f6f8;
  background-color: var(--light-gray);
}

body .App.dark {
  background-color: #121721;
  background-color: var(--midnight);
}

.container {
  max-width: 1110px;
  margin: 0 auto;
}

.button {
  border-radius: 8px;
  background-color: #f4f6f8;
  background-color: var(--light-gray);
  color: #5964e0;
  color: var(--violet);
  padding: 1rem 2rem;
  text-transform: capitalize;
  font-weight: 700;
  font-size: 1.2rem;
  cursor: pointer;
}

.button:hover {
  background-color: rgba(147, 155, 244, 0.5);
}

.button.primary {
  background-color: #5964e0;
  background-color: var(--violet);
  color: #fff;
  color: var(--white);
}

.button.primary:hover {
  background-color: #939bf4;
  background-color: var(--light-violet);
}

/* Header */
header {
  background-image: url(/static/media/bg-pattern-header.f3307602.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #5964e0 !important;
  background-color: var(--violet) !important;

  border-bottom-left-radius: 150px;
  padding: 2rem 0;
}

header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.theme-switch {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.toggler {
  background-color: #fff;
  background-color: var(--white);
  width: 30px;
  height: 15px;
  border-radius: 1rem;

  display: flex;
  align-items: center;

  padding: 0.1rem;
  cursor: pointer;
}

.toggle {
  border-radius: 50%;
  width: 12px;
  height: 100%;
  background-color: #5964e0;
  background-color: var(--violet);
}

.toggle.dark {
  margin-left: auto;
}

.toggle.light {
  margin-right: auto;
}

/* Cards */
.cards {
  display: grid;
  grid-template-columns: repeat(3, 350px);
  grid-gap: 4.5rem 2.5rem;
  gap: 4.5rem 2.5rem;
  margin-top: 5rem;
}

/* Card */
.card {
  background-color: #fff;
  background-color: var(--white);
  border-radius: 1rem;
  padding: 2rem;
  position: relative;
  min-height: 250px;
}

.card.dark {
  background-color: #19202d;
  background-color: var(--very-dark-blue);
}

.card.dark h2 {
  color: #f4f6f8;
  color: var(--light-gray);
}

.card__header {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  top: -10%;
  border-radius: 0.5rem;
}

.card__body {
  margin: 1rem auto 2rem;
}

.card__body h2 {
  margin: 0.5rem auto;
  color: #19202d;
  color: var(--very-dark-blue);
}

.card__body p {
  color: #6e8098;
  color: var(--dark-gray);
}

.card__footer p {
  color: #5964e0;
  color: var(--violet);
}

/* Detail */
.detail section {
  background-color: #fff;
}

.detail-header {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  border-radius: 0.5rem;
}

.detail-header__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 140px;
  height: 140px;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
}

.detail-header__logo img {
  width: 80%;
}

.detail-header__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  padding-right: 3rem;
}

.detail-header__info-company {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

.detail-content {
  margin-top: 5rem;
  border-radius: 0.5rem;
  padding: 3rem;
  background-color: #fff;
}

.detail-content section {
  margin-bottom: 2rem;
}

.detail-content h3 {
  text-transform: capitalize;
  margin-bottom: 1rem;
}

.detail-content p,
.detail-content li {
  color: #6e8098;
  color: var(--dark-gray);
}

.detail-content ul,
.detail-content ol {
  margin-left: 2rem;
}

.detail-content li {
  margin: 1rem 0;
}

.detail-content__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.detail-content__header-company p {
  color: #6e8098;
  color: var(--dark-gray);
}

.detail-content__header-company h1 {
  font-size: 28px;
  margin: 0.5rem 0;
}

.detail-content__header-company p:last-of-type {
  color: #5964e0;
  color: var(--violet);
}

.detail-content__role ol {
  list-style: none;
  counter-reset: list-counter;
}

.detail-content__role ol li {
  counter-increment: list-counter;
}

.detail-content__role ol li::before {
  content: counter(list-counter);
  color: #5964e0;
  color: var(--violet);
  font-weight: bold;
}

.detail.dark section {
  background-color: #19202d;
  background-color: var(--very-dark-blue);
  color: #fff;
}

.detail.dark .detail-content {
  background-color: #19202d;
  background-color: var(--very-dark-blue);
}

.detail.dark .detail-header__info-cta .button {
  background-color: #121721;
  background-color: var(--midnight);
  color: #fff;
  color: var(--white);
}

.detail.dark .detail-header__info-cta .button:hover {
  background-color: #9daec2;
  background-color: var(--gray);
}

